	import back from '@/components/back'

	export default {
		components: {
			back
		},
		data() {
			return {
				title: "编辑商品",
				isRecommend: false,
				//list:null,
				list: null,
				recomMsg:null,
				info:null,
				fromIM:this.$store.state.recomMsg.fromIM
			}
		},
		created() {
			if(this.$store.state.editRecomDetails) {
				this.info = this.$store.state.editRecomDetails
				this.list = this.$store.state.editRecomDetails.ListDetails
			} else {
				this.list = []
			}
			if(this.$store.state.recomMsg){
				this.recomMsg = this.$store.state.recomMsg
			}
		},
		mounted() {

		},
		beforeDestroy() {
			this.$store.dispatch('pushEditRecomDetails', null)
			window.onscroll = null
		},
		methods: {
			/*addGoods(){
				//添加商品
			},*/
			subNum(n) {
				//减1
				if(this.list[n].GoodsNum > 1) {
					this.list[n].GoodsNum--
				}
			},
			addNum(n) {
				//加1
				this.list[n].GoodsNum++
			},
			deleteItem(n) {
				//删除
				if(this.list.length > 1) {
					this.list.splice(n, 1)
				} else {
					this.$toast("商品数量已最低，无法删除该商品")
				}
			},
			saveGoods() {
				/*this.recomMsg = {
					DataSource: 2,//数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
					DataId: 11,//数据来源Id
				}*/
				let _url = "PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend"
// 				if(this.recomMsg && this.recomMsg.DataSource == 1){
// 					//常用
// 					_url = "PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommendUsed"
// 				}else if(this.recomMsg && this.recomMsg.DataSource == 2){
// 					//历史
// 					_url = "PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend"
// 				}else{
// 					
// 				}
				let ListArr = []
				this.list.forEach(v => {
					ListArr.push({
						GoodsSource:v.GoodsSource?v.GoodsSource:3,	//商品类型(1-平台服务，2-专科商品，3-平台商品)
						GoodsId:v.GoodsId,	//商品Id/服务项Id
						SpecialistGoodsId:v.GoodsSource == 3 ? 0:v.SpecialistGoodsId,//专科商品Id(商品类型1,2,4 必填，3传0)
						SpecialistHosGroupId:v.SpecialistHosGroupId?v.SpecialistHosGroupId:0, //专科医联体id
						HosGroupName:v.SpecialistHosGroupId?v.HosGroupName:'',  //专科医联体名称
						GoodsName:v.GoodsName,	//商品名称/服务项名称
						GoodsNum:v.GoodsNum,	//商品数量
						DrId:v.DrId || 0,
						DrName:v.DrName||'',
						GoodsSpecId:v.GoodsSpecId || "",	//商品规格Id
						GoodsSpecName:v.GoodsSpecName || "",	//规格名称
					})
				})
				this.$post(_url, {
					GoodsRecomId:0,	//商品推荐Id  this.info.GoodsRecomId,
					DrId: this.info.DrId,
					DrName: this.info.DrName,
					GoodsRecomDetail: ListArr
				}).then(o => {
					if(!o){
						return
					}
					this.$store.dispatch('pushrecomMsg',{
						DataSource: 2,//数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
						DataId: o,//数据来源Id
						fromIM:this.fromIM
					})
					this.$router.replace(`/prefRecommend/reDetail?tId=1`)
				})
			}
		}
	}



